  padding: 0;
  font-family: "Nunito Sans";
  color: white;
  text-decoration: none;
}


::-webkit-scrollbar {
  width: 0px;
  height: 0px;
}


input[type="date"]::-webkit-calendar-picker-indicator {
  background: url("./components/todo/images/calendar.png") no-repeat center;
  background-size: contain;
  cursor: pointer;
}




.App {
  display: flex;
  padding: 1cm;
